* {
  font-family: Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  width: 100%;
  font-size: 1.2rem;
  color: grey;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.card {
  align-items: center;
  border: 1px solid grey;
  border-radius: 6px;
  overflow: hidden;
  margin: 10px;
}

.card p {
  text-align: center;
  padding: 5px;
}
img {
  max-height: 170px;
}
